export interface InputParameterInterface {
  name: string;
  type: 'string' | 'number' | 'boolean' | 'array';
  required?: boolean;
  items?: Array<InputParameterInterface>;
}

export interface ArrayParameterInterface extends InputParameterInterface {
  type: 'array';
  items: Array<InputParameterInterface>;
}

export interface CreateActionInterface {
  identifier: string;
  version: string;
  tenantIdentifier: string;
  name: string;
  description?: string;
  connectorIdentifier: string;
  config: {
    [key: string]: { [key: string]: string | object } | string | object;
  };
  inputParameters: Array<InputParameterInterface>;
  outputParameters: unknown;
}

export interface ActionInterface extends CreateActionInterface {
  createdAt: Date;
}
